// all

.backdrop {
  z-index: 9999;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
  transition: opacity 1000ms, visibility 1000ms;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal__close {
  width: 20px;
  height: 20px;
  background: transparent;
  border-color: transparent;
  position: fixed;
  color: var(--darkest);
  top: 20px;
  right: 20px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (max-width: 479px) {
    width: 16px;
    height: 16px;
    top: 8px;
    right: 8px;
  }
}

// all

.modal-buy-now {
  padding: 32px 40px 32px 40px;
  width: 568px;
  max-height: 95%;
  background-color: var(--lightest);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  position: absolute;
  overflow-y: auto;
  top: 50%;
  left: 50%;
  // transform: scale(1) translateX(100%);
  transform: translate(-50%, -50%);
  transition: transform 1000ms;

  @media screen and (max-width: 767px) {
    padding: 28px 30px 28px 28px;
    max-width: 280px;
    max-height: 95%;
    overflow-y: auto;
  }
}

.modal-title-buy-now {
  font-family: 'Seymour One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  letter-spacing: 0.03em;
  color: var(--darkest);

  margin-bottom: 24px;
  margin-top: 0;

  @media screen and (max-width: 479px) {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
}

.container-products {
  display: flex;
  justify-content: center;
  align-items: center;
}

.buy-now__list {
  font-style: normal;
  margin: 0px;
  padding: 0px;

  @media screen and (max-width: 767px) {
    padding-left: 42px;
