.product-name {
  margin-top: 92px;
  width: 100%;
  height: 20px;

  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--dark);
}

.container-info {
  margin-top: 100px;

  @media screen and (max-width: 767px) {
    margin-top: 90px;
  }
}

.modal-field-buy {
  max-width: 368px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  margin-bottom: 16px;
  position: relative;
  text-align: start;
  transition: border-color 250ms cubic-bezier (0.4, 0, 0.2, 1);

  @media screen and (max-width: 479px) {
    margin-bottom: 12px;
  }
}

.modal-field-buy input {
  background-color: transparent;
  border-radius: 10px;
  height: 42px;
  padding-left: 16px;
  outline-color: var(--darkest);
  width: 100%;

  @media screen and (max-width: 479px) {
    height: 32px;
    padding-left: 12px;
  }
}

.modal-input-buy {
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  border: none;
}

.modal-textarea-buy {
  max-width: 368px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;

  @media screen and (max-width: 479px) {
  }
}

.modal-textarea-input-buy {
  resize: none;
  width: 100%;
  height: 84px;
  resize: none;
  padding: 11px 16px;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;

  @media screen and (max-width: 479px) {
    height: 64px;
    padding: 8px 12px;
  }
}

.modal-textarea-input-buy::placeholder,
.modal-input-buy::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.01em;

  color: var(--darkest);

  @media screen and (max-width: 479px) {
    font-size: 12px;
    line-height: 140%;
  }
}

.modal-button-buy {
  background: var(--chicken);
  border-radius: 10px;
  color: var(--darkest);
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;

  display: block;
  box-shadow: none;
  border-radius: 10px;
  border-color: transparent;

  transition: background-color 250ms linear, color 250ms linear;

  margin: 0 auto;
  padding: 12px 43px;

  @media screen and (max-width: 479px) {
    padding: 7px 43px;
    font-size: 12px;
    line-height: 1.25;
  }

  &:hover,
  &:focus {
    outline: transparent;
    background: var(--hover);
    color: var(--light);
  }
}
