.modal-franchise {
  padding: 32px 40px 32px 40px;
  max-width: 568px;
  max-height: 95%;
  background-color: var(--lightest);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border: 2px solid var(--chicken);
  border-radius: 10px;
  position: fixed;
  overflow-y: auto;
  // position: absolute;
  top: 50%;
  left: 50%;
  // transform: scale(1) translateX(100%);
  transform: translate(-50%, -50%);
  transition: transform 1000ms;

  @media screen and (max-width: 479px) {
    padding: 28px 30px 28px 28px;
    width: 280px;
    max-height: 95%;
    overflow-y: auto;
  }
}

.modal-franchise-title {
  font-family: 'Seymour One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  letter-spacing: 0.03em;
  color: var(--black);

  margin-bottom: 24px;
  margin-top: 0;

  @media screen and (max-width: 479px) {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
}

.modal-franchise-info {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;

  margin-bottom: 32px;

  @media screen and (max-width: 479px) {
    font-size: 14px;
    line-height: 140%;
    margin-bottom: 24px;
  }
}

.modal-franchise-field {
  max-width: 368px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--khaki);
  border-radius: 10px;
  margin-bottom: 16px;
  position: relative;
  text-align: start;
  transition: border-color 250ms cubic-bezier (0.4, 0, 0.2, 1);

  @media screen and (max-width: 479px) {
    margin-bottom: 12px;
  }
}

.modal-franchise-field input {
  background-color: transparent;
  border-radius: 10px;
  height: 42px;
  padding-left: 16px;
  outline-color: var(--black);
  width: 100%;

  @media screen and (max-width: 479px) {
    height: 32px;
    padding-left: 12px;
  }
}

.modal-franchise-input {
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  border: none;
}

.modal-textarea {
  max-width: 368px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;

  @media screen and (max-width: 479px) {
  }
}

.modal-textarea-input {
  resize: none;
  width: 100%;
  height: 84px;
  resize: none;
  padding: 11px 16px;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--khaki);
  border-radius: 10px;

  @media screen and (max-width: 479px) {
    height: 64px;
    padding: 8px 12px;
  }
}

.modal-textarea-input::placeholder,
.modal-franchise-input::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.01em;

  color: var(--black);

  @media screen and (max-width: 479px) {
    font-size: 12px;
    line-height: 140%;
  }
}

.modal-franchise-button {
  background: var(--chicken);
  border-radius: 10px;
  color: var(--black);
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.06em;

  display: block;
  box-shadow: none;
  border-radius: 10px;
  border-color: transparent;

  transition: background-color 250ms linear, color 250ms linear;

  margin: 0 auto;
  padding: 12px 43px;

  @media screen and (max-width: 479px) {
    padding: 7px 43px;
    font-size: 12px;
    line-height: 1.25;
  }

  &:hover,
  &:focus {
    outline: transparent;
    background: var(--hover);
    color: var(--light);
  }
}
